@import url(https://fonts.googleapis.com/css?family=Open+Sans|Mrs+Sheppards);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #1c2532;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

div, h1, h2, h3, h4, h5, h6 {
  box-sizing: border-box;
}

.app {
  box-sizing: border-box;
  width: 100vw;
  min-height: 100vh;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}


.charts-container {
  max-width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: center;
          justify-content: center;
  margin: 0 auto;
}

.content-header {
  width: 100%;
  padding: 30px 0;
  font-family: Open Sans;
  font-size: 30px;
  color: #2a352f;
}

.content {
  margin: 0 auto;
  width: 1280px;

  max-width: 100%;
  overflow-x: scroll;

  padding: 30px 20px;
  font-family: Open Sans;
  font-size: 16px;
}


.js-plotly-plot {
  display: block;
  width: 100%;
  height: 100%;
}


@media (max-width: 500px) {
  .header,
  .content,
  .win-rate,
  .player-faction { 
    width: 100vw;
    overflow-x: hidden;
  }

  .player-faction .main-svg{
    margin-left: -58px;
  }

}
.welcome-mat {
    position: relative;
    box-sizing: border-box;

    width: 100%;
    height: 600px;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.welcome-mat:before {
    content: ' ';
    position: absolute;
    
    background-size: cover;

    display: block;

    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    width: 100%;
    height: 100%;
    z-index: 100;
}

.welcome-mat__header-1,
.welcome-mat__header-2 {
    position: relative;

    font-family: Mrs Sheppards;
    font-weight: 200;
    font-size: 60px;
    letter-spacing: 4px;
    color: #728041;

    z-index: 200;

    margin: 0;
    padding: 0;
    text-align: center;
    text-transform: uppercase;
    font-style: normal;
}

.welcome-mat__header-1 {

}

.welcome-mat__header-2 {
    
}


.chart-card {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-align-items: center;
            align-items: center;
    margin: 20px;
    max-width: 100%;
    min-width: 330px;
    box-sizing: border-box;
    background-image: -webkit-linear-gradient(#728041, transparent 50px);
    padding: 1px;
    border-radius: 13px;
}

.chart-card__content {
    background-color: #1c2532;
    box-sizing: border-box;
    border-radius: 12px;

    width: 100%;
}

.chart-card__name {
    font-size: 16px;
    color: #728041;
    margin-bottom: -30px;
    min-width: 100%;
    text-align: center;
}

.modebar{
    display: none !important;
}
.chart-module {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    max-width: 600px;
}

.player-faction {
    background-color: #87ce71;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  
  .player-faction .main-svg {
    margin-left: -15px;
    margin-top: -25px;
  }

.mini-leaderboard {
    box-sizing: border-box;
    min-width: 350px;
    max-width: 600px;

    list-style: none;
    padding: 0 20px;
    color: #728041;
}

.mini-leaderboard__leader {
    position: relative;

    box-sizing: border-box;
    width: 100%;

    border-radius: 12px;
    border: 1px solid #728041;
    padding-top: 12px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.mini-leaderboard__leader-crown {
    position: absolute;
    top: -22px;
    width: 100%;
    font-size: 30px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    
    padding: 0;
    margin: 0;

}

.mini-leaderboard__leader-name {
    font-size: 18px;
    width: 100%;
    text-align: center;
    text-transform: capitalize;
    letter-spacing: 1px;
    font-weight: 500;
    margin: 0;
}

.mini-leaderboard__leader-stats {
    position: relative;

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    text-align: center;

    margin: 10px 0;
    padding: 10px 0;
}

.leader-stat__label {
    font-size: 12px;
    font-weight: 500;
    padding: 0;
    margin: 0;
}

.leader-stat__value {
    font-size: 14   px;
    padding: 0;
    margin: 0;
    color: rgb(255, 127, 14);
}

.mini-leaderboard__leader-faction {
    font-size: 14px;
    text-align: center;
    padding: 0 0 7px 0;
    margin: 0;
    line-height: 0.5;
}

.mini-leaderboard__player {
    position: relative;
    display: block;
    padding: 15px 20px;
    box-sizing: border-box;
    width: 100%;
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}

.mini-leaderboard__player-faction {
    font-size: 10px;
    font-weight: 500;
    margin-left: 5px;
    line-height: 1.6;
}

.mini-leaderboard__player-wins {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    color: rgb(255, 127, 14);
    font-size: 14px;
    text-align: right;
    
}

.mini-leaderboard__leader-stats:before,
.mini-leaderboard__player:after {
    content: ' ';
    position: absolute;
    box-sizing: border-box;

    bottom: 0;

    height: 1px;
    width: 100%;
    background-image: -webkit-radial-gradient(#728041, transparent 175px);
}

.mini-leaderboard__leader-stats:before {
    top: -1px;
}

.mini-leaderboard__player:last-of-type:after {
    content: none;
}


.meta-stats {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    margin: 0 auto;
    
    background-size: cover;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
}

.meta-stats:before {
    content: ' ';
    position: absolute;

    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    width: 100%;
    height: 100%;
}
